<template>
  <router-link class="header__cart" href="#" aria-label="Корзина с товарами" :to="{name: 'cart'}">
    <svg :class="{'cart-indicator--animation': isProductAmountChanged}" width="19" height="24">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span
      class="header__count"
      :class="{'cart-indicator--animation': isProductAmountChanged}"
      aria-label="Количество товаров"
    >
      {{ cartProductsAmount }}
    </span>
  </router-link>
</template>

<script setup>
import { useStore } from 'vuex';
import { computed, ref, watch } from 'vue';

const store = useStore();

const cartProductsAmount = computed(() => store.state.cartProducts.length);
const isProductAmountChanged = ref(false);
watch(cartProductsAmount, () => {
  isProductAmountChanged.value = true;
  let timer;
  clearTimeout(timer);
  timer = setTimeout(() => {
    isProductAmountChanged.value = false;
  }, 3000);
});
</script>
<style scoped>
  .cart-indicator--animation {
    animation: cart-indicator-scale 3s linear forwards;
  }

  @keyframes cart-indicator-scale {
    0% {
      transform: scale(1);
    }
    20% {
      transform: scale(1);
    }
    30% {
      transform: scale(1.5);
    }
    40% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
    60% {
      transform: scale(1);
    }
    70% {
      transform: scale(1.5);
    }
    80% {
      transform: scale(1);
    }
    90% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }

  .header__cart {
    transition: transform 0.1s ease-in-out;
  }
  .header__cart:active {
    transform: scale(1.3);
  }
</style>
