<template>
  <div class="form__counter">
    <button
      type="button" aria-label="Убрать один товар"
      @click.prevent="count -= 1"
      :disabled="count <= 1 || count === ''"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="count" name="count">

    <button type="button" aria-label="Добавить один товар" @click.prevent="count += 1">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script setup>
import { computed } from 'vue';

const props = defineProps({
  currentAmount: {
    type: Number,
  },
});
const emit = defineEmits(['update:currentAmount']);

const count = computed({
  get() {
    return props.currentAmount;
  },
  set(value) {
    emit('update:currentAmount', Number(value));
  },
});
</script>

