    <div class="error-message">
      Что-то пошло не так...
    </div>
    <a class="link-reload" @click.prevent="store.dispatch('loadCart')" href="#">
      попробовать еще раз
    </a>
  </main>
  <main v-else-if="cartProducts.length === 0" class="content container container--empty">
    <div class="empty-cart-page">
      <h1 class="empty-cart-page__title">Корзина пустует :(</h1>
      <div class="empty-cart-page__content">
        а тем временем в
        <router-link class="empty-cart-page__link" :to="{name: 'main'}">каталоге</router-link>
        ждут крутые товары
      </div>
    </div>
  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ cartProducts.length }} {{ productAmountFormat(cartProducts.length) }}
        </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent>
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="product in cartProducts" :cart-product="product" :key="product.id"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
