          </p>
          <router-link :to="{name: 'order'}" v-slot="{ navigate }" custom>
            <button class="cart__button button button--primery" type="submit" @click="navigate">
              Оформить заказ
            </button>
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script setup>
import { useStore } from 'vuex';
import { computed } from 'vue';
import CartItem from '@/components/CartItem.vue';
import productAmountFormat from '@/helpers/productAmountFormat';
import numberFormat from '../helpers/numberFormat';

const store = useStore();
const cartProducts = computed(() => store.state.cartProducts);
const cartTotalPrice = computed(() => (store.getters.cartTotalPrice));
</script>

