<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="doOrder">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormInputText
              title="ФИО"
              placeholder="Введите ваше полное имя"
              :error="formError.name"
              v-model:value="formData.name"
            />
            <BaseFormInputText
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
              :error="formError.address"
              v-model:value="formData.address"
            />
            <BaseFormInputText
              title="Телефон"
              placeholder="Введите ваш телефон"
              :error="formError.phone"
              v-model:value="formData.phone"
            />
            <BaseFormInputText
              title="Email"
              placeholder="Введи ваш Email"
              :error="formError.email"
              v-model:value="formData.email"
            />
            <BaseFormInputText
              input-type="textarea"
              title="Комментарий к заказу"
              placeholder="Ваши пожелания"
              :error="formError.comment"
              v-model:value="formData.comment"
            />
          </div>
          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <BaseOptionItem
                v-for="deliver in deliveries"
                :key="deliver.id"
                item-type="Доставка"
                :item-title="deliver.title"
                :price="deliver.price"
                :input-value="deliver.id"
                v-model:current-value="currentDeliveryType"
              />
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <BaseOptionItem
                v-for="payment in payments"
                :key="payment.id"
                item-type="Оплата"
                :item-title="payment.title"
                :input-value="payment.id"
                v-model:current-value="currentPaymentType"
              />
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="product in products" :product="product" :key="product.product.id"/>
          </ul>
          <CartTotal
            :product-amount="products.length"
            :cart-price="cartTotalPrice"
            :delivery-price="currentDeliveryPrice"
          />

          <button
            class="cart__button button button--primery"
            type="submit"
            :disabled="isOrderSending"
          >
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="errorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ errorMessage }}. Попробуйте отправить снова или перезагрузите страницу.
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script setup>
import {
  computed, onMounted, reactive, ref, watch,
} from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import axios from 'axios';
import BaseFormInputText from '@/components/BaseFormInputText.vue';
import BaseOptionItem from '@/components/BaseOptionItem.vue';
import OrderItem from '@/components/OrderItem.vue';
import { API_BASE_URL } from '@/config';
import CartTotal from '@/components/CartTotal.vue';

const store = useStore();
const router = useRouter();
// состояние формы
const formData = reactive({});
const formError = ref({});
const errorMessage = ref('');
const isOrderSending = ref(false);
// инфо о доставке
const deliveriesData = ref(null);
const deliveries = computed(() => (deliveriesData.value ? deliveriesData.value : []));
const currentDeliveryType = ref(null);
const currentDeliveryPrice = computed(() => (deliveriesData.value
  ? Number(deliveries.value.find((el) => (el.id === currentDeliveryType.value)).price)
  : 0
));
// инфо о способах оплаты
const paymentsData = ref(null);
const payments = computed(() => (paymentsData.value ? paymentsData.value : []));
const currentPaymentType = ref(null);

const products = computed(() => store.state.cartProducts);
const cartTotalPrice = computed(() => (store.getters.cartTotalPrice));
const getDeliveries = () => {
  axios
    .get(`${API_BASE_URL}/api/deliveries`)
    .then((response) => {
      deliveriesData.value = response.data;
    });
};

const getPayments = () => {
  axios
    .get(`${API_BASE_URL}/api/payments`, { params: { deliveryTypeId: currentDeliveryType.value } })
    .then((response) => {
      paymentsData.value = response.data;
      currentPaymentType.value = payments.value[0].id;
    });
};
// добавление заказа
const doOrder = () => {
  isOrderSending.value = true;
  formError.value = {};
  errorMessage.value = '';
  axios
    .post(
      `${API_BASE_URL}/api/orders`,
      {
        name: formData.name,
        address: formData.address,
        phone: formData.phone,
        email: formData.email,
        deliveryTypeId: currentDeliveryType.value,
        paymentTypeId: currentPaymentType.value,
        comment: formData.comment,
      },
      { params: { userAccessKey: store.state.userAccessKey } },
    )
    .then((response) => {
      store.commit('updateOrderInfo', response.data);
      store.commit('updateCartProducts', []);
      isOrderSending.value = false;
      router.push({ name: 'orderInfo', params: { id: response.data.id } });
    })
    .catch((error) => {
      formError.value = error.response.data.error.request || {};
      errorMessage.value = error.response.data.error.message;
    })
    .then(() => {
      isOrderSending.value = false;
    });
};

onMounted(() => {
  getDeliveries();
});
watch(deliveriesData, () => {
  currentDeliveryType.value = deliveriesData.value[0].id;
});
watch(currentDeliveryType, () => {
  getPayments();
});

</script>

