import { createStore } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default createStore({
  state: {
    cartProducts: [],
    userAccessKey: null,

    orderInfo: null,

    cartIsLoading: false,
    isLoadingFailed: false,

    addingProduct: false,
    isDone: false,
    isAddingFailed: false,
    addingError: '',
  },
  getters: {
    cartTotalPrice(state) {
      return state.cartProducts.length === 0 ? 0 : state.cartProducts.reduce((total, item) => (
        total + item.quantity * item.price
      ), 0);
    },
  },
  mutations: {
    updateCartProducts(state, items) {
      state.cartProducts = items;
    },
    updateLoadingStatus(state, value) {
      state.cartIsLoading = value;
    },
    updateLoadingResult(state, value) {
      state.isLoadingFailed = value;
    },

    updateAddingStatus(state, value) {
      state.addingProduct = value;
    },
    updateAddingResult(state, value) {
      state.isAddingFailed = value;
    },
    updateAddingError(state, error) {
      state.addingError = error;
    },
    updateAddingDone(state, value) {
      state.isDone = value;
    },

    updateUserAccessKey(state, value) {
      state.userAccessKey = value;
    },
    updateOrderInfo(state, items) {
      state.orderInfo = items;
    },
  },
  actions: {
    loadCart(context) {
      context.commit('updateLoadingStatus', true);
      context.commit('updateLoadingResult', false);
      axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProducts', response.data.items);
          context.commit('updateLoadingStatus', false);
        })
        .catch(() => {
          context.commit('updateLoadingResult', true);
        })
        .then(() => {
          context.commit('updateLoadingStatus', false);
        });
    },
    addCartProduct(context, {
      productId, colorId, sizeId, quantity,
    }) {
      context.commit('updateAddingStatus', true);
      context.commit('updateAddingResult', false);
      axios
        .post(
          `${API_BASE_URL}/api/baskets/products`,
          {
            productId,
            colorId,
            sizeId,
            quantity,
          },
          { params: { userAccessKey: context.state.userAccessKey } },
        )
        .then((response) => {
          context.commit('updateCartProducts', response.data.items);
          context.commit('updateAddingStatus', false);
          context.commit('updateAddingDone', true);
        })
        .catch((error) => {
          context.commit('updateAddingError', Object.values(error.response.data.error.request)[0]);
          context.commit('updateAddingResult', true);
          let timer;
          clearTimeout(timer);
          timer = setTimeout(() => {
            context.commit('updateAddingResult', false);
          }, 1500);
        })
        .then(() => {
          context.commit('updateAddingStatus', false);
          let timer;
          clearTimeout(timer);
          timer = setTimeout(() => {
            context.commit('updateAddingDone', false);
          }, 2000);
        });
    },
    updateCartProductAmount(context, { basketItemId, quantity }) {
      axios
        .put(
          `${API_BASE_URL}/api/baskets/products`,
          {
            basketItemId,
            quantity,
          },
          { params: { userAccessKey: context.state.userAccessKey } },
        )
        .then((response) => {
          context.commit('updateCartProducts', response.data.items);
        });
    },
    deleteCartProduct(context, basketItemId) {
      axios
        .delete(
          `${API_BASE_URL}/api/baskets/products`,
          {
            data: {
              basketItemId,
            },
            params: { userAccessKey: context.state.userAccessKey },
          },
        )
        .then((response) => {
          context.commit('updateCartProducts', response.data.items);
        });
    },
  },
  modules: {
  },
});
