<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ productsCount }} {{ productAmountFormat(productsCount) }}
        </span>
        <div class="content__product-amount">
          <p class="content__product-amount--text">Товаров на странице:</p>
          <label class="form__label form__label--select form__label--product-amount">
            <select
              class="form__select form__select--product-amount"
              name="category"
              v-model="limit"
            >
              <option :value="9">9</option>
              <option :value="12">12</option>
              <option :value="18">18</option>
              <option :value="27">27</option>
            </select>
          </label>
        </div>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter
        v-model:current-page="currentPage"
        v-model:min-price="minPrice"
        v-model:max-price="maxPrice"
        v-model:color-ids="colorIds"
        v-model:category-id="categoryId"
        v-model:material-ids="materialIds"
        v-model:season-ids="seasonIds"
      />
      <section class="catalog">
          <div v-if="loadingStatus.isLoading" class="catalog__list loading-status">
            <img class="loading-icon" src="../../public/img/svg/loading.svg" width="80" alt="">
          </div>
        <div v-else-if="loadingStatus.isFailed" class="catalog__list error-status">
            <div class="error-message">
              Упс... {{ loadingStatus.error }}...
            </div>
            <a class="link-reload" type="button" href="#" @click.prevent="getProducts">
              попробовать еще раз
            </a>
          </div>
        <ProductList v-else :products="products"/>
        <BasePagination
          :pages="pages"
          v-model:current-page="currentPage"
        />
      </section>
    </div>
  </main>
</template>

<script setup>
import {
  computed, reactive, ref, watch,
} from 'vue';
import axios from 'axios';
import ProductFilter from '@/components/ProductFilter.vue';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import { API_BASE_URL } from '@/config';
import productAmountFormat from '@/helpers/productAmountFormat';
// входные параметры для загрузки
const currentPage = ref(1);
const limit = ref(12);
// параметры фильтрации
const minPrice = ref(null);
const maxPrice = ref(null);
const categoryId = ref(0);
const materialIds = ref([]);
const seasonIds = ref([]);
const colorIds = ref([]);
// свойства-параметры загрузки данных
const loadingStatus = reactive({
  isLoading: false,
  isFailed: false,
  error: '',
});
// данные о списке товаров
const productsData = ref(null);
const products = computed(() => (productsData.value || []));
// данные пагинации
const pages = ref(null);
const productsCount = ref(null);
// получаю инфо о списке товаров
const getProducts = () => {
  loadingStatus.isLoading = true;
  loadingStatus.isFailed = false;
  axios
    .get(`${API_BASE_URL}/api/products`, {
      params: {
        page: currentPage.value,
        limit: limit.value,
        minPrice: minPrice.value,
        maxPrice: maxPrice.value,
        categoryId: categoryId.value,
        materialIds: materialIds.value,
        seasonIds: seasonIds.value,
        colorIds: colorIds.value,
      },
    })
    .then((response) => {
      productsData.value = response.data.items;
      pages.value = response.data.pagination.pages;
      productsCount.value = response.data.pagination.total;
    })
    .catch((error) => {
      loadingStatus.isFailed = true;
      loadingStatus.error = error.response.data.error.message;
    })
    .then(() => {
      loadingStatus.isLoading = false;
    });
};

getProducts();

watch([
  currentPage,
  minPrice,
  maxPrice,
  categoryId,
  colorIds,
  materialIds,
  seasonIds,
], () => {
  getProducts();
});

watch(limit, () => {
  currentPage.value = 1;
  getProducts();
});
</script>

<style>
.loading-status,.error-status {
  width: 100%;
  min-height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content__product-amount {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.content__product-amount--text {
  margin-right: 10px;
}

.form__select--product-amount {
  height: 35px;
  padding-top: 5px;
  padding-left: 10px;
  padding-bottom: 5px;
  font-size: 14px;
}

.form__label--product-amount::after {
  right: 10px;
}

.error-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.error-message {
  font-size: 32px;
}

.link-reload {
  font-size: 32px;
  color: #e02d71;
  text-decoration: underline;
}

.loading-icon {
  animation: loading 1s linear infinite;
}

@keyframes loading {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
