<template>
  <main v-if="loadingStatus.isLoading" class="content container container--empty error-status">
    <img class="loading-icon" src="../../public/img/svg/loading.svg" width="80" alt="">
  </main>
  <main
    v-else-if="loadingStatus.isFailed"
    class="content container container--empty error-status"
  >
    <div class="error-message">
      Что-то пошло не так...
    </div>
    <a class="link-reload" @click.prevent="getOrderInfo" href="#">попробовать еще раз</a>
  </main>

  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина.
            На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                {{ orderInfo.paymentType }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="product in products" :product="product" :key="product.id"/>
          </ul>
          <CartTotal
            :product-amount="products.length"
            :cart-price="orderInfo.totalPrice - deliveryPrice"
            :delivery-price="deliveryPrice"
          />
        </div>
      </form>
    </section>
  </main>
</template>

<script setup>
import { useRoute } from 'vue-router';
import { computed, reactive, watch } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import OrderItem from '@/components/OrderItem.vue';
import CartTotal from '@/components/CartTotal.vue';

const store = useStore();
const route = useRoute();

const loadingStatus = reactive({
  isLoading: false,
  isFailed: false,
});
const orderInfo = computed(() => (store.state.orderInfo ? store.state.orderInfo : []));
const products = computed(() => (store.state.orderInfo ? store.state.orderInfo.basket.items : []));
const deliveryPrice = computed(() => (store.state.orderInfo
  ? Number(orderInfo.value.deliveryType.price)
  : 0
));
const getOrderInfo = () => {
  loadingStatus.isLoading = true;
  loadingStatus.isFailed = false;
  axios
    .get(`${API_BASE_URL}/api/orders/${route.params.id}`, {
      params: { userAccessKey: store.state.userAccessKey },
    })
    .then((response) => {
      store.commit('updateOrderInfo', response.data);
      loadingStatus.isLoading = false;
    })
    .catch(() => {
      loadingStatus.isFailed = true;
    })
    .then(() => {
      loadingStatus.isLoading = false;
    });
};

if (!store.state.orderInfo || route.params.id !== store.state.orderInfo.id) {
  getOrderInfo();
}
watch(route, () => {
  getOrderInfo();
});
</script>
