<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: props.product.id}}">
      <img :src="currentImage" :alt="props.product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ productInfo.title }}
      </a>
    </h3>

    <span class="catalog__price">{{ numberFormat(productInfo.price) }} ₽</span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in productInfo.colors" :key="color.id">
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            :name="color.id"
            :value="color.color.id"
            v-model="currentColor"
          >
          <span class="colors__value" :style="{backgroundColor: color.color.code}"></span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script setup>
import { computed, ref } from 'vue';
import numberFormat from '@/helpers/numberFormat';

const props = defineProps({
  product: {
    required: true,
  },
});
// меняю значение gallery если оно null
const productInfo = computed(() => ({
  ...props.product,
  colors: props.product.colors
    .map((el) => (el.gallery ? el : { ...el, gallery: [{ file: { url: '' } }] })),
}));
const currentColor = ref(productInfo.value.colors[0].color.id);
// вычисляю ссылку на изображение по выбранному цвету товара
const currentImage = computed(() => (
  productInfo.value.colors.find((el) => (el.color.id === currentColor.value)).gallery[0].file.url
));

</script>

<style scoped>
.colors__value {
  border: 1px solid #e2e2e2;
}
</style>
