<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow "
        :class="{'pagination__link--disabled': currentPage === 1}"
        href="#"
        aria-label="Предыдущая страница"
        @click.prevent="doDecreasePage"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="page in pages" :key="page">
      <a
        class="pagination__link"
        :class="{'pagination__link--current': currentPage === page}"
        href="#"
        @click.prevent="doChangePage(page)"
      >
        {{ page }}
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        :class="{'pagination__link--disabled': currentPage === pages}"
        href="#"
        aria-label="Следующая страница"
        @click.prevent="doIncreasePage"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script setup>
const props = defineProps({
  pages: {
    required: true,
  },
  currentPage: {
    type: Number,
    required: true,
  },
});
const emit = defineEmits(['update:currentPage']);
// методы перехода на другую страницу
const doChangePage = (page) => {
  emit('update:currentPage', page);
};
const doIncreasePage = () => {
  if (props.currentPage === props.pages) return;
  emit('update:currentPage', props.currentPage + 1);
};
const doDecreasePage = () => {
  if (props.currentPage === 1) return;
  emit('update:currentPage', props.currentPage - 1);
};

</script>
