<template>
  <aside class="filter">
    <form class="filter__form form" action="#" method="get" @submit.prevent="doFilterProducts">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model="currentMinPrice">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model="currentMaxPrice">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" name="category" v-model="currentCategoryId">
            <option value="0">Все категории</option>
            <option
              v-for="category in productFeatures.categories"
              :value="category.id"
              :key="category.id">{{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors colors__list">
          <li class="colors__item" v-for="color in productFeatures.colors" :key="color.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only"
                type="checkbox"
                name="цвет"
                :value="color.code"
                v-model="color.checked"
              >
              <span class="colors__value" :style="{backgroundColor: color.code}"></span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="material in productFeatures.materials"
            :key="material.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="материал"
                :value="material.id"
                v-model="material.checked"
              >
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="season in productFeatures.seasons" :key="season.id">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collection"
                :value="season.title"
                v-model="season.checked"
              >
              <span
                class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button
        class="filter__submit button button--primery"
        type="submit"
      >
        Применить
      </button>
      <button
        v-if="isFilterParameterChosen"
        class="filter__reset button button--second"
        type="button"
        @click="resetFilterProducts"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script setup>
import {
  computed, onMounted, reactive, ref, watch,
} from 'vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

const props = defineProps({
  currentPage: {
    type: Number,
    required: true,
  },
  minPrice: {
    required: false,
  },
  maxPrice: {
    required: false,
  },
  categoryId: {
    required: false,
  },
  colorIds: {
    type: Array,
    required: false,
  },
  materialIds: {
    type: Array,
    required: false,
  },
  seasonIds: {
    type: Array,
    required: false,
  },
});
const emit = defineEmits([
  'update:currentPage',
  'update:minPrice',
  'update:maxPrice',
  'update:categoryId',
  'update:colorIds',
  'update:materialIds',
  'update:seasonIds',
]);
// данные о характеристиках товаров
const productFeatures = reactive({
  categories: [],
  colors: [],
  materials: [],
  seasons: [],
});
// методы для получения инфо о характеристиках товаров
const getCategoriesData = () => {
  axios
    .get(`${API_BASE_URL}/api/productCategories`)
    .then((response) => {
      productFeatures.categories = response.data.items;
    });
};
// получаю список цветов
const getColorsData = () => {
  axios
    .get(`${API_BASE_URL}/api/colors`)
    .then((response) => {
      productFeatures.colors = response.data.items;
    });
};
// получаю список материалов
const getMaterialsData = () => {
  axios
    .get(`${API_BASE_URL}/api/materials`)
    .then((response) => {
      productFeatures.materials = response.data.items;
    });
};
// получаю список сезонов
const getSeasonsData = () => {
  axios
    .get(`${API_BASE_URL}/api/seasons`)
    .then((response) => {
      productFeatures.seasons = response.data.items;
    });
};
// текущие параметры фильтрации
const currentMinPrice = ref('');
const currentMaxPrice = ref('');
const currentCategoryId = ref(0);
// вычисляю id выбранных параметров
const currentColorIds = computed(() => (
  productFeatures.colors
    .filter((col) => (col.checked))
    .map((col) => (col.id))
));
const currentMaterialIds = computed(() => (
  productFeatures.materials
    .filter((mat) => (mat.checked))
    .map((mat) => (mat.id))
));
const currentSeasonIds = computed(() => (
  productFeatures.seasons
    .filter((s) => (s.checked))
    .map((s) => (s.id))
));
// проверяем выбран ли какой-либо из пар-ров фильтрации
const isFilterParameterChosen = computed(() => {
  let result = false;
  if (
    currentMinPrice.value !== ''
    || currentMaxPrice.value !== ''
    || Number(currentCategoryId.value) !== 0
    || currentColorIds.value.length !== 0
    || currentMaterialIds.value.length !== 0
    || currentSeasonIds.value.length !== 0
  ) { result = true; }
  return result;
});
// метод применения фильтрации
const doFilterProducts = () => {
  emit('update:currentPage', 1);
  emit('update:minPrice', currentMinPrice.value);
  emit('update:maxPrice', currentMaxPrice.value);
  emit('update:colorIds', currentColorIds.value);
  emit('update:materialIds', currentMaterialIds.value);
  emit('update:seasonIds', currentSeasonIds.value);
  emit('update:categoryId', currentCategoryId.value);
};
// метод сброса выбранных чекбоксов
const resetCheckedProps = () => {
  // eslint-disable-next-line no-param-reassign
  productFeatures.seasons.forEach((el) => (delete el.checked));
  // eslint-disable-next-line no-param-reassign
  productFeatures.materials.forEach((el) => (delete el.checked));
  // eslint-disable-next-line no-param-reassign
  productFeatures.colors.forEach((el) => (delete el.checked));
};
// метод сброса параметров фильтра
const resetFilterProducts = () => {
  emit('update:minPrice', '');
  emit('update:maxPrice', '');
  emit('update:colorIds', []);
  emit('update:materialIds', []);
  emit('update:seasonIds', []);
  emit('update:categoryId', 0);
  resetCheckedProps();
};

onMounted(() => {
  getCategoriesData();
  getColorsData();
  getMaterialsData();
  getSeasonsData();
});
// обновляю состояние DOM
watch(props, () => {
  currentMinPrice.value = props.minPrice;
  currentMaxPrice.value = props.maxPrice;
  currentCategoryId.value = props.categoryId;
});

</script>

<style scoped>
.colors__label:focus .colors__value::before,
.colors__label:hover .colors__value::before,
.colors__radio:checked ~ .colors__value::before {
  border-color: #737373;
}

.colors__value {
  border: 1px solid #e2e2e2;
}
</style>
