<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="product.image" width="120" height="120" :alt="product.product.title">
    </div>
    <h3 class="product__title">
      {{ product.product.title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span><i :style="{backgroundColor: product.color.color.code}" class="cart__item--color"></i>
        {{ product.color.color.title }}
      </span>
    </p>
    <p class="product__info product__info--size">
      Размер:
      <span>
        {{ product.size.title }}
      </span>
    </p>
    <span class="product__code">Артикул: {{ product.product.id }}</span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар"
              @click.prevent="count -= 1"
              :disabled="count === 1"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model="count" name="count">

      <button
        type="button" aria-label="Добавить один товар"
        @click.prevent="count += 1"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ numberFormat(product.price * product.quantity) }} ₽
    </b>

    <button
      class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteCartItem(product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script setup>
import { computed } from 'vue';
import { useStore } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

const props = defineProps({
  cartProduct: {
    type: Object,
    required: true,
  },
});
const store = useStore();

const product = computed(() => {
  let imageSrc;
  if (props.cartProduct.color.gallery) {
    imageSrc = props.cartProduct.color.gallery[0].file.url;
  } else {
    imageSrc = '';
  }
  return { ...props.cartProduct, image: imageSrc };
});
const count = computed({
  get() {
    return props.cartProduct.quantity;
  },
  set(value) {
    // eslint-disable-next-line no-restricted-globals
    if (value < 1 || value === '' || isNaN(value)) {
      return;
    }
    store.dispatch('updateCartProductAmount', { basketItemId: props.cartProduct.id, quantity: value });
  },
});
const deleteCartItem = (basketItemId) => {
  store.dispatch('deleteCartProduct', basketItemId);
};
</script>

<style scoped>
.product__del {
  cursor: pointer;
}

.product__pic {
  grid-row: 1/5;
}

.product__info--size {
  margin: 0;
  grid-column: 2/3;
  grid-row: 3/4;
}

.product__code {
  grid-row: 4/5;
}

.cart__item--color {
  border: 1px solid #e2e2e2;
}
</style>
