<template>
  <main v-if="loadingStatus.isLoading" class="content container loading-status">
    <img class="loading-icon" src="../../public/img/svg/loading.svg" width="80" alt="">
  </main>

  <main v-else-if="loadingStatus.isFailed" class="content container error-status">
    <div class="error-message">
      Упс... {{ loadingStatus.error }}...
    </div>
    <a class="link-reload" href="#" @click.prevent="getProduct">
      Попробовать еще раз
    </a>
  </main>

  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ productCategory.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ productData.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570" height="570"
            :src="currentImage"
            :alt="productData.title"
          >
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="color in productData.colors" :key="color.color.id">
            <a
              href="#" class="pics__link"
              :class="{'pics__link--current': color.color.id === currentColor}"
              @click.prevent="doChangeColor(color.color.id)"
            >
              <img width="98" height="98" :src="color.gallery[0].file.url" :alt="productData.title">
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ productData.id }}</span>
        <h2 class="item__title">
          {{ productData.title }}
        </h2>
        <div class="item__form">
          <form
            class="form" action="#" method="POST"
            @submit.prevent="doAddProductToCart({
              productId: productData.id.toString(), colorId: currentColor.toString(),
              sizeId: currentSize.toString(), quantity: currentProductAmount.toString()
            })">
            <div class="item__row item__row--center">
              <ProductCounter v-model:current-amount="currentProductAmount"/>

              <b class="item__price">
                {{ numberFormat(productData.price * currentProductAmount) }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li
                    class="colors__item"
                    v-for="color in productData.colors"
                    :key="color.color.id"
                  >
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        :name="productData.id"
                        :value="color.color.id"
                        v-model="currentColor"
                      >
                      <span class="colors__value" :style="{backgroundColor: color.color.code}">
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" name="category" v-model="currentSize">
                    <option
                      v-for="size in productSizes"
                      :value="size.id"
                      :key="size.id"
                    >
                      {{ size.title }}
                    </option>
                  </select>
                </label>
              </fieldset>
            </div>
            <button
              class="item__button button button--primery cart-button"
              :class="{'isDone': store.state.isDone}"
              type="submit">
              <span class="cart-button__add">В корзину</span>
              <span class="cart-button__added"><nobr>Товар добавлен!</nobr></span>
              <img
                class="btn-icon"
                src="../../public/img/svg/white-shopping-cart.svg"
                width="24"
                alt=""
              >
            </button>
            <div class="error-container" v-if="store.state.isAddingFailed">
              {{ store.state.addingError }} :(
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a
              class="tabs__link"
              :class="{'tabs__link--current': currentTab === 'info'}"
              href="#"
              @click.prevent="doChangeTab('info')"
            >
              Информация о товаре
            </a>
          </li>
          <li class="tabs__item">
            <a
              class="tabs__link"
              :class="{'tabs__link--current': currentTab === 'ship'}"
              href="#"
              @click.prevent="doChangeTab('ship')"
            >
              Доставка и возврат
            </a>
          </li>
        </ul>

        <div class="item__content" v-if="currentTab === 'info'">
          {{ productData.content }}
          Информация о товаре
        </div>
        <div class="item__content" v-if="currentTab === 'ship'">
          Информация о доставке и возврате товара
        </div>
      </div>
    </section>
  </main>
</template>

<script setup>
import { onBeforeRouteUpdate, useRoute } from 'vue-router';
import {
  computed, reactive, ref, watch, toRaw,
} from 'vue';
import axios from 'axios';
import ProductCounter from '@/components/ProductCounter.vue';
import { API_BASE_URL } from '@/config';
import numberFormat from '@/helpers/numberFormat';
import { useStore } from 'vuex';

const route = useRoute();
const store = useStore();
// данные о товаре
const product = ref(null);
const productData = computed(() => {
  let productNew = {};
  if (product.value) {
    const obj = toRaw(product.value);
    productNew = { ...obj, colors: obj.colors.map((el) => (el.gallery ? el : { ...el, gallery: [{ file: { url: '' } }] })) };
  }
  return productNew;
});
// свойства-параметры загрузки данных
const loadingStatus = reactive({
  isLoading: false,
  isFailed: false,
  error: '',
});
// загрузка инфо о товаре
const getProduct = () => {
  loadingStatus.isLoading = true;
  loadingStatus.isFailed = false;
  axios
    .get(`${API_BASE_URL}/api/products/${route.params.id}`)
    .then((response) => {
      product.value = response.data;
      loadingStatus.isLoading = false;
    })
    .catch((error) => {
      loadingStatus.isFailed = true;
      loadingStatus.error = error.response.data.error.message;
    })
    .then(() => {
      loadingStatus.isLoading = false;
    });
};
// характеристики товара
const productCategory = computed(() => (product.value ? product.value.category : {}));
const productSizes = computed(() => (product.value ? product.value.sizes : {}));
// текущие характеристики (выбранные)
const currentColor = ref(null);
const currentSize = ref(null);
const currentProductAmount = ref(1);
// // вычисляю ссылку на изображение по выбранному цвету товара
const currentImage = computed(() => (product.value
  ? productData.value.colors.find((el) => (el.color.id === currentColor.value)).gallery[0].file.url
  : ''
));
// текущий таб
const currentTab = ref('info');
// добавление товара в корзину
const doAddProductToCart = ({
  productId, colorId, sizeId, quantity,
}) => {
  store.dispatch('addCartProduct', {
    productId, colorId, sizeId, quantity,
  });
};
// переключение параметров
const doChangeColor = (colorId) => {
  currentColor.value = colorId;
};
const doChangeTab = (value) => {
  currentTab.value = value;
};

onBeforeRouteUpdate(() => {
  getProduct();
});

watch(route, () => {
  getProduct();
}, { deep: true, immediate: true });

watch(product, () => {
  currentColor.value = productData.value.colors[0].color.id;
  currentSize.value = productSizes.value[0].id;
});
</script>

<style scoped>
.pics__link img {
  height: 100%;
}

.colors__value {
  border: 1px solid #e2e2e2;
}

.cart-button {
  margin-bottom: 3px;
  position: relative;
  overflow: hidden;
}

.cart-button__add {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cart-button__added {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  //color: #150c26;
  opacity: 0;
}

.btn-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-630%, -50%);
}

.isDone .btn-icon {
  animation: cart-icon-move 1s ease-in-out forwards;
}

.isDone .cart-button__added {
  animation: added-text 2s ease-in-out forwards;
}

.isDone .cart-button__add {
  animation: add-text 2.3s ease-in-out forwards;
}

@keyframes cart-icon-move {
  0% {
    transform: translate(-630%, -50%);;
  }
  30% {
    transform: translate(-50%, -50%);
  }
  70% {
    transform: translate(-50%, -50%);
  }
  100% {
    transform: translate(600%, -50%);
  }
}

@keyframes added-text {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes add-text {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  85% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.error-container {
  max-width: 200px;
  font-size: 11px;
  font-weight: bold;
  color: #e02d71;
  text-align: center;
}
</style>

<style>
.button--primery:active {
  transform: scale(0.9);
}
</style>
