<template>
  <li class="options__item">
    <label class="options__label">
      <input
        class="options__radio sr-only" type="radio"
        :name="props.itemType"
        :value="props.inputValue"
        v-model="currentValue"
      >
      <span class="options__value">
        {{ props.itemTitle }} <b v-if="props.price">{{ deliveryPriceFormat(props.price) }}</b>
      </span>
    </label>
  </li>
</template>

<script setup>
import deliveryPriceFormat from '@/helpers/deliveryPriceFormat';
import { computed } from 'vue';

const props = defineProps({
  itemType: { required: true },
  itemTitle: { required: true },
  inputValue: {},
  currentValue: {},
  price: { required: false, default: '' },
});
const emit = defineEmits(['update:currentValue']);
const currentValue = computed({
  get() {
    return props.currentValue;
  },
  set(value) {
    emit('update:currentValue', value);
  },
});

</script>
